* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
}

.container {
  margin: 2rem;
  text-align: center;
}

.nav {
  background-color: #2c3e50; /* Dark navy background */
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

.site-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #ecf0f1; /* Light gray for the title */
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1.5rem;
}

.nav a {
  color: #ecf0f1; /* Light gray for links */
  text-decoration: none;
  font-size: 1.2rem;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.nav a:hover {
  background-color: #34495e; /* Slightly lighter shade of navy on hover */
  color: #1abc9c; /* Bright green for hover text */
}

.nav li.active a {
  background-color: #1abc9c; /* Active link background */
  color: white;
}

.nav li.active a:hover {
  background-color: #16a085; /* Darker green on hover for active links */
}

/* Media query for responsive design */
@media (max-width: 768px) {
  .nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .nav ul {
    flex-direction: column;
    gap: 0.75rem;
  }

  .nav a {
    font-size: 1rem;
    padding: 0.5rem 1rem;
  }
}
